.sliceRoot{
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0;
   -ms-flex-positive: 4;
       flex-grow: 4;
   width: 91vh;
     height: auto;
}
.imagen{
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 4;
      flex-grow: 4;
}

@media all and (max-width: 1140px) {
  .imagen  { margin-top: 42vh;}
}

@media all and (max-width: 800px) {
  .sliceRoot  { width:70vh;}
}
