.header{
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;


  -webkit-animation-name:menuUno;


          animation-name:menuUno;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
.headerDos{
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:distribute;
      justify-content:space-around;
  -ms-flex-align: center;
      align-items: center;
}
.headerProjects{
  width: 95%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:end;
      justify-content:flex-end;
  -ms-flex-align: center;
      align-items: center;
}
.headerAbout{
  width: 95%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:start;
      justify-content:flex-start;
  -ms-flex-align: center;
      align-items: center;
}
.headerContact{
  width: 95%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
}
.menuUno{
  display: inline-block;
  list-style: none;
  -webkit-transition: 1s  ease;
  -o-transition: 1s  ease;
  transition: 1s  ease;
}
.imgLogo{
  width: 80%;
  height: auto;
}
.imgLogoPeque{
  left:0;
  width:50%;
  height: auto;
}
.menuUno li a {
  text-decoration: none;

}

.menuUno .textMenu{
  -webkit-transition: 500ms 200ms ease;
  -o-transition: 500ms 200ms ease;
  transition: 500ms 200ms ease;
}
.menuUno{
  -webkit-animation-name:menuUno;
          animation-name:menuUno;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  /* animation-delay:.5s ; */
}
.headerProjects .menuUno{
  padding-left: 7%;
}
.headerAbout .menuUno{
  padding-right: 7%;
}
.headerContact .menuUno{
  padding-right: 3%;
  padding-left: 3%;
}
@-webkit-keyframes menuUno {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@keyframes menuUno {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@-webkit-keyframes escala {
  0%{
    -webkit-transform: scale(.3);
            transform: scale(.3);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes escala {
  0%{
    -webkit-transform: scale(.3);
            transform: scale(.3);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.menuUno li{ padding-bottom: 9px;  }
.menuUno:hover .textMenu{
  font-size: 30px;
  font-weight: 700;
}
.logoPeque{

border-color: white;

}
.aboutPadre{
  height:auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.aboutHijoUno{

  /* border: 1px solid white; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-negative: 1.5;
      flex-shrink: 1.5;
}

.logoCel{
  display:none;
}

.hamburguesa{
  display: none;

}
.headerCell{
  display: none;
}

@media screen and (max-width: 740px) {

.logoPeque{display: none;}
  .logo{display:none;}
  .logoCel{display:inline-block; width: 100;}
}
@media screen and (max-width: 600px) {
  .header  {
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: rgba(10, 17, 36, .8);
    position:absolute;
    top: 0;
    left: -100%;
    width: 100%;
    right:  100%;
    bottom: 0;
    z-index: 2;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    -webkit-animation-name: header;
            animation-name: header;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  .headerDos{
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .header.active{
    left: 0;
  }
  .headerCell{
    display:inline;
  }
  .hamburguesa{
    width: 75px;
    height: 75px;
    line-height: 40px;
    text-align: center;
    display:-ms-flexbox;
    display:flex;
    background: #070D19;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    position: absolute;
    right:0;
    top:0;
    -webkit-transition: 500ms 200ms ease;
    -o-transition: 500ms 200ms ease;
    transition: 500ms 200ms ease;
  }
  .hamburguesa:hover{
    width: 90px;
    height: 90px;
  }

  .menuUno li{ padding-bottom: 9px; -webkit-transition: 500ms 200ms ease; -o-transition: 500ms 200ms ease; transition: 500ms 200ms ease;
    -webkit-animation-name:menuUnoli;
            animation-name:menuUnoli;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }
  @-webkit-keyframes .menuUno li {
    0%{
      opacity: 1;
    }
    100%{opacity: 0}
  }
  @keyframes .menuUno li {
    0%{
      opacity: 1;
    }
    100%{opacity: 0}
  }
  .menuUno:hover li{
    -webkit-transform: skewX(10deg);
        -ms-transform: skewX(10deg);
            transform: skewX(10deg);
    font-size: 30px;
    font-weight: 700;
  }
}
/* header */



