
.descripcionProjecto{
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 2;
      flex-shrink: 2;
}
.hijoDosUno {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-line-pack: center;
      align-content: center;
}
.imagenPrincipal{
  width: 100%;
  height: auto;
}

.flecha{
  width: 60px;
  top: 70%;
   right: 0;
   position: fixed;
}
.flechaUp{
  width: 60px;
  top: 40%;
   right: 0;
   position: fixed;
}
.flechaAbajo{
  width: 35px;
  margin: auto;
  margin-left:17px ;
}

.flechaArriba{
  width: 35px;
  margin: auto;
  margin-left:17px ;
  -webkit-transform: rotate(180deg);
}


.tituloProjecto {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.estiloFuente{
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:start;
      justify-content:flex-start;
  -ms-flex-direction: row;
      flex-direction: row;
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  list-style: none;
  text-decoration: none;
  padding-left:20px;
  padding-right:20px;
}

.estiloFuente h3 {
  /* text-decoration: none;
  list-style: none; */
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-style: italic;

}

.estiloFuente h2 {
    font-style: italic;
}
.fecha{
  color: white;
  margin-left: 56px;
  list-style: none;
  text-decoration: none;
}
.numberStep{
  color: #858a93;
  margin-left: 56px;
  font-style: italic;
  list-style: none;cursor: pointer;
  text-decoration: none;-webkit-transition: .8s  ease;-o-transition: .8s  ease;transition: .8s  ease;
}
.numberStep:hover{
  color: white;
}
.pasosProceso {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}
.hijoTresTres {
  color: white;
  -ms-flex-positive: 5;
      flex-grow: 5;
  font-size: 28px;
  text-align: left;
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  padding-left: 20px;
  padding-right: 20px;
}


@media all and (max-width: 1366px) {
  /* .imagenPrincipal    { width:750px; } */
  .hijoTresTres {  font-size: 21px;}
}

@media all and (max-width: 1140px) {
  /* .imagenPrincipal    { width:400px; } */
  .padre  { -ms-flex-direction: column; flex-direction: column;}
  .estiloFuente  {  -ms-flex-pack:center;  justify-content:center; }
  .hijoTresTres {  font-size: 17px;}
}
@media screen and (max-width: 900px) {
  .fecha  {  font-size: 19px;}
  .numberStep  {  font-size: 19px;}
  .hijoTresTres {  font-size: 19px;}
}
@media screen and (max-width: 740px) {
  .fecha  {  font-size: 16px;}
  .numberStep  {  font-size: 16px;}
  .hijoTresTres {  font-size: 16px;}
}



